* {
  box-sizing: border-box;
margin: 0;
padding: 0;
text-decoration: none;
list-style-type: none;
}



body {
    background-color: $mobileBackgroundColor;
    font-size: 20px;
      color:$mobileColor;
    @include xs{
      background-color: $xsBackgroundColor;
      color:$xsColor;
      font-size: 18px;
    }
    @include sm {
      background-color: $smBackgroundColor;
      color:$smColor;
      font-size: 18px;
    }
    @include md{
      background-color: $mdBackgroundColor;
      color:$mdColor;
      font-size: 16px;
    }
}

/*--------------------------------------------------------------------------------------------------------------------------------*/

.container {
  max-width: 1156px;
  margin: 0 auto;
  padding: 0 1.5em;
}
